/**=====================
     Responsive CSS Start
==========================**/

@media (max-width: 1836px) {
    .pie-chart {
        .apexcharts-legend-text {
            line-height: unset;
        }
    }
}

@media (max-width: 1800px) {
    // chat page
    .caller-img {
        display: none;
    }
}

@media screen and (max-width: 1660px) {
    .photos {
        ul {
            li {
                width: 26%;
            }
        }
    }

    .bar-chart {
        canvas {
            width: 100% !important;
        }
    }

    .chat-box {
        .chat-history {
            .call-icons {
                ul {
                    li {
                        border: 1px solid $dark-gray;
                    }
                }
            }

            .total-time {
                h2 {
                    color: $dark-gray;
                }
            }

            .call-content {
                .btn {
                    width: auto;
                    margin: 0 auto;
                }

                > div {
                    z-index: 7;
                    background-color: rgba($white, 0.75);
                    background-blend-mode: overlay;
                    width: 100%;
                    padding: 30px;
                }
            }

            .receiver-img {
                margin-top: 25px;
            }
        }
    }

    .chat-left-aside {
        .people-list {
            height: 520px;
        }
    }

    .chat-box {
        .chat-right-aside {
            .chat {
                .chat-msg-box {
                    height: 440px;
                }
            }
        }

        .people-list {
            ul {
                max-height: 450px;
                overflow: auto;
            }
        }
    }

    .call-chat-body {
        .chat-box {
            .people-list {
                ul {
                    max-height: 550px;
                    overflow: auto;
                }
            }

            overflow: hidden;
            height: 625px;
        }
    }

    .caller-img {
        position: absolute;
        width: 100%;
        max-width: 100%;
        left: 15px;

        img {
            opacity: 0.7;
        }
    }

    // timeline small page
    .timeline-small {
        .media {
            .timeline-round {
                &.timeline-line-1 {
                    &:after {
                        bottom: -78px;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 1600px) {
    .page-wrapper {
        &.horizontal-wrapper {
            &.material-type {
                .left-header {
                    .level-menu,
                    .mega-menu {
                        .nav-link {
                            svg {
                                margin-right: 0;
                            }

                            span {
                                display: none;
                            }
                        }
                    }
                }

                .page-header {
                    max-width: 1320px;
                }

                .page-body-wrapper {
                    .sidebar-wrapper,
                    .page-body {
                        max-width: 1320px;
                    }
                }
            }
        }
    }

    // chat page
    .chat-box {
        .chat-right-aside {
            .chat {
                .chat-header {
                    .chat-menu-icons {
                        li {
                            margin-right: 10px;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 1535px) {
    .custome-1-bg,
    .custome-2-bg,
    .custome-3-bg,
    .custome-4-bg {
        padding: 15px 0 15px 20px !important;
    }
}

@media screen and (max-width: 1428px) {
    .category-box {
        .content-category {
            padding: 60px 20px 25px 20px;
        }
    }
}

@media screen and (max-width: 1400px) {
    .datepicker-dashboard {
        .datepicker--days-names {
            font-size: 18px;
            margin: 18px 0;
        }

        .datepicker--cells-days {
            height: 200px;
        }

        .datepicker {
            height: 360px;
        }

        .datepicker--nav-action {
            @include pseudowh($width: 30px, $height: 30px);
        }

        .datepicker--nav-title {
            font-size: calc(15px + (18 - 15) * ((100vw - 320px) / (1920 - 320)));
        }
    }

    .btn-group {
        .btn {
            padding: 0.375rem 0.75rem;
        }
    }
}

@media screen and (max-width: 668px) {
    .custome-1-bg,
    .custome-2-bg,
    .custome-3-bg,
    .custome-4-bg {
        padding: 15px 0 15px 20px !important;
    }
}

@media screen and (max-width: 1380px) {
    .single-section {
        .description-section {
            .description-details {
                .menu-part {
                    .rooms-box {
                        td:nth-child(2) {
                            width: 100% !important;
                            display: block !important;
                        }
                    }
                }
            }
        }
    }

    .single-section {
        .description-section {
            .description-details {
                .menu-part {
                    .rooms-box {
                        td:first-child {
                            width: 100%;
                            display: block;

                            img {
                                width: 100% !important;
                            }
                        }

                        .room-title {
                            margin: 20px 0;
                        }

                        .room-detail {
                            padding: 0;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 1660px) and (min-width: 1200px) {
    .xl-none {
        display: none;
    }

    .xl-60 {
        max-width: 60%;
        flex: 0 0 60%;
    }

    .xl-50 {
        max-width: 50%;
        flex: 0 0 50%;
    }

    .xl-100 {
        max-width: 100%;
        flex: 0 0 100%;
    }

    .xl-23 {
        max-width: 23%;
        flex: 0 0 23%;
    }

    .xl-25 {
        max-width: 25%;
        flex: 0 0 25%;
    }

    .xl-40 {
        max-width: 40%;
        flex: 0 0 40%;
    }

    .xl-4 {
        max-width: 33.33%;
        flex: 0 0 33.33%;
    }
}

@media (max-width: 1280px) {
    .img-gallery-set1,
    .img-gallery-set5 {
        display: none;
    }

    .box-layout.page-wrapper.horizontal-wrapper {
        .page-header {
            padding-left: unset;
            max-width: unset;
            left: unset;
        }

        .page-body-wrapper {
            width: unset;
        }
    }
}

@media screen and (max-width: 1580px) and (min-width: 1200px) {
    .docs-buttons,
    .docs-toggles {
        flex: 0 0 100%;
        max-width: 100%;
    }
}

@media screen and (max-width: 1445px) {
    .user-table {
        tbody {
            tr {
                td:nth-child(2) {
                    min-width: 185px;
                }

                td:nth-child(3) {
                    min-width: 160px;
                }
            }
        }
    }
}

@media (max-width: 1470px) {
    //edit-profile
    .edit-profile {
        .profile-title {
            .media {
                display: block;
                margin-bottom: 10px;

                .media-body {
                    margin-left: unset;
                    margin-top: 10px;
                }
            }
        }
    }
}

@media screen and (max-width: 1366px) and (min-width: 1200px) {
    .flot-chart-container-small {
