    }

    // chat
    .chat-body {
        padding: 10px;
    }

    .chat-box {
        .chat-history {
            .call-content {
                .btn {
                    font-size: 15px;
                }
            }

            .total-time h2 {
                font-size: 30px;
            }

            .receiver-img {
                margin-top: 30px;
            }

            .call-icons {
                margin-top: 20px;
                margin-bottom: 20px;

                ul {
                    li {
                        @include pseudowh($width: 50px, $height: 50px);
                        padding: 7px;
                    }
                }
            }
        }

        .user-image {
            @include pseudowh($width: 30px, $height: 30px);
            margin-top: 3px;
            margin-right: 3px;
        }

        .about {
            margin-top: 0;
        }

        .border-tab.nav-tabs {
            .nav-item {
                a {
                    padding: 13px 4px !important;
                    color: $semi-dark !important;
                    letter-spacing: 0;
                    font-size: 12px !important;
                    line-height: 6;
                }
            }
        }

        .chat-menu {
            .people-list {
                ul.list {
                    max-height: 376px;
                    overflow-y: auto;
                }
            }

            .user-profile {
                .image {
                    .avatar {
                        img {
                            @include pseudowh($width: 100px, $height: 100px);
                        }
                    }

                    .icon-wrapper {
                        @include pseudowh($width: 30px, $height: 30px);
                        font-size: 13px;
                    }
                }
            }
        }
    }

    .chat-left-aside {
        .people-list {
            height: 420px;

            ul {
                max-height: 376px;
                overflow-y: auto;
            }
        }

        .status-circle {
            top: 4px;
            left: 36px;
        }
    }

    //blog scss
    .blog-box {
        .blog-details-main {
            .blog-bottom-details {
                margin: 20px 10px;
            }

            .blog-social {
                li {
                    padding: 0px 10px;
                }
            }
        }
    }

    .blog-bottom-content {
        display: none;
    }

    .blog-box.blog-shadow {
        .blog-details {
            padding: 10px;
        }
    }

    .browser-widget {
        .media-img {
            margin-left: auto;
            margin-right: auto;
            margin-bottom: 15px;
        }

        img {
            height: 50px;
        }
    }

    // email-app
    .email-wrap {
        .email-left-aside {
            .email-app-sidebar {
                .media {
                    display: block;

                    .media-size-email {
                        width: 100%;
                        text-align: center;

                        .mr-3 {
                            margin-right: 0 !important;
                        }
                    }

                    .media-body {
                        text-align: center;
                    }
                }
            }
        }

        .email-right-aside {
            .email-body {
                .attachment {
                    ul {
                        li {
                            img {
                                @include pseudowh($width: 73px, $height: 73px);
                            }
                        }
                    }
                }

                .email-top {
                    line-height: 50px;

                    .float-right {
                        display: none !important;
                    }

                    h5 {
                        padding: 13px 0;
                    }
                }

                .inbox {
                    max-height: 668px;
                    overflow: auto;

                    .media {
                        display: block;

                        .media-size-email {
                            width: 100%;
                            text-align: center;

                            .mr-3 {
                                margin-right: 0 !important;
                            }
                        }
                    }

                    .media-body {
                        text-align: center;

                        h6 {
                            font-size: 14px;
                        }
                    }
                }

                .email-compose {
                    .cke_contents.cke_reset {
                        max-height: 144px;
                        width: 99%;
                    }

                    .compose-border {
                        margin: 0;
                    }
                }
            }
        }
    }

    // alert page
    .alert-dismissible {
        p {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            max-width: 181px;
        }
    }

    // touchspin page
    .bootstrap-touchspin {
        .touchspin-vertical-tab {
            .input-group {
                width: 96% !important;
            }
        }
    }
}

