@media only screen and (max-width: 1366px) and (min-width: 1200px) {
    .page-wrapper {
        &.horizontal-wrapper {
            .left-header {
                flex: 0 0 32%;
                max-width: 32%;
            }

            .page-header {
                .header-wrapper {
                    .nav-right {
                        flex: 0 0 54%;
                        max-width: 54%;
                    }
                }
            }

            &.material-type {
                .page-header {
                    max-width: 1140px;
                }

                .page-body-wrapper {
                    .sidebar-wrapper,
                    .page-body {
                        max-width: 1140px;
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 1199.98px) {
    .left-header .mega-menu-container .mega-box .svg-icon ~ div h6 {
        display: none;
    }

    .wizard-4 {
        ul {
            &.anchor {
                padding-left: 40px;
                padding-right: 40px;
            }
        }
    }

    .file-sidebar {
        padding-right: 12px;
    }

    .col-xl-7.b-center.bg-size {
        top: 0;
        height: 100vh;
        opacity: 0.5;
        position: fixed;
    }

    .login-card {
        background: transparent;
    }

    .knowledgebase-bg {
        height: 400px;
    }

    .knowledgebase-search {
        .form-inline {
            width: 400px;
            padding: 7px 45px;

            svg {
                top: 14px;
            }
        }
    }

    .btn-group {
        .btn {
            padding: 0.375rem 1.25rem;
        }
    }

    .link-section > div {
        &.active {
            > h6 {
                &:before {
                    content: "\f107";
                    transition: content 0.3s ease;
                }
            }
        }

        > h6 {
            position: relative;

            &:before {
                @include pos($pos: absolute, $content: "\f105");
                font-family: fontAwesome;
                right: 10px;
                font-size: 18px;
                transition: content 0.3s ease;
            }
        }
    }

    .left-header {
        .mega-menu-container {
            position: fixed;
