                    width: 38%;
                }
            }
        }

        .total {
            li {
                .count {
                    width: 38%;
                }
            }
        }
    }
}

@media screen and (max-width: 991px) {
    .order-box {
        .sub-total {
            .shipping-class {
                .shopping-checkout-option {
                    width: 38%;
                }
            }
        }
    }

    .page-wrapper {
        &.compact-wrapper {
            .page-header {
                .header-wrapper {
                    .toggle-sidebar {
                        display: block;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 575px) {
    .checkout {
        .checkout-details {
            padding: 15px;
        }
    }

    .order-box {
        .sub-total {
            .shipping-class {
                .shopping-checkout-option {
                    width: 50%;
                }
            }

            li {
                .count {
                    width: 50%;
                }
            }
        }
    }
}

